@import "theme/local/local_child_theme_imports"; // <--------- LocalStyles import

/*--------------------------------------------------------------
## Login
--------------------------------------------------------------*/
body.login div#login h1 a {
    background-image: url(https://codearosa.de/wp-content/uploads/2018/12/Favicon-Codearosa.png); //Add your own logo image in this url
    padding-bottom: 30px;
}

body.login {
    background-color: $secondary;
}

p#backtoblog {
    display: none;
}

#loginform {
    @extend .secondary-neumorph;
    border-radius: $border-radius;
}

.login #backtoblog a,
.login #nav a {
    color: $secondary;
}

/* Submit  input button*/
.wp-core-ui .button-primary,
.wp-core-ui .button-primary:hover,
.wp-core-ui .button-primary:focus,
.wp-core-ui .button-primary:active {
    @extend .secondary-neumorph;
    width: 100%;
    color: $primary !important;
    text-transform: uppercase !important;
    margin-top: 1em;
    font-size: large;
}

/*inputstyles*/
.login input {
    @include secondary-neumorph-active;
    background-color: $secondary !important;
}

/*dashicons color*/
.dashicons {
    color: $primary !important;
}

/*login error color*/
.login #login_error {
    border-left-color: $primary !important;
    border-left: 4px solid $primary !important;
}

form,
input {
    /*force yellow background in chrome*/
    -webkit-box-shadow: 0 0 0 100px rgba(250, 250, 250, 0) inset !important;
    border: 1px solid $primary;
    background-image: none !important;
    color: rgb(0, 0, 0) !important;
}

input:focus,
textarea,
button:focus {
    border: none !important;
}